.app-store_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    /* z-index: 1500px; */
    padding: 0;

    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.app-store_btn img {
    margin-right: 12px;
}

.app-store_btn span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}

@media (min-width: 1440px) {
    .app-store_btn:hover {
        background: #CACACA;
    }
}

@media (max-width: 1023px) {
    .app-store_btn span {
        font-size: 14px;
        line-height: 16px;
    }
}


.google-play_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: rgba(0, 0, 0, 1);
    color: #ffffff;
    height: 48px;
    border: none;
    border-radius: 40px;
}

.google-play_btn img {
    margin-right: 12px;
}

.google-play_btn span {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
}
.header-frame {
    position: absolute;
    width: 252px;
    height: 384px;
    right: 1px;
    top: 60px;
    padding: 40px 40px;
    z-index: 100;

    border: 1px solid #FFFFFF;
    background: rgba(255, 255, 255, 0.7);
    -webkit-backdrop-filter: blur(100px);
            backdrop-filter: blur(100px);
    border-radius: 10px;
    animation: 0.5s ease 0s normal forwards 1 fadein;
}

@keyframes fadein {
    0% { opacity:0; }
    66% { opacity:0; }
    100% { opacity:1; }
}

.header-frame_link {
    margin-bottom: 20px;
}

.header-frame a {
    font-family: Raleway;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;

    color: #000000;
    text-decoration: none;
}

.header-frame .app-store_btn {
    width: 150px;
    margin-bottom: 20px;
}

.header-frame .google-play_btn {
    width: 150px;
    opacity: 0.1;
}

.header-frame .google-play_btn span {
    font-size: 14px;
    line-height: 16px;
}

.active-frame_link {
    font-weight: bold;
}

@media (max-width: 1023px) {
    .header-frame {
        top: 100px;
    }
}

@media (max-width: 767px) {
    .header-frame {
        top: 80px;
    }
}

@media (max-width: 479px) {
    .header-frame {
        top: 60px;
    }
}
.modal-window {
    position: fixed;
    width: 600px;
    height: 500px;
    z-index: 3000;
    left: 50%;
    top: 50vh;
    transform: translate(-50%,-50%);
    border-radius: 10px;
    background-color: #ffffff;
    color: #000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-shadow: 0px 2px 60px 20px rgba(163, 146, 236, 0.2);
    animation: 0.5s ease 0s normal forwards 1 fadein;
}

@keyframes fadein {
    0% { opacity:0; }
    66% { opacity:0; }
    100% { opacity:1; }
}

.modal-window img {
    width: 249px;
    height: 249px;
}

.modal-window p {
    margin-top: 12px;
    width: 336px;
    height: 92px;

    font-family: Raleway;
    font-style: normal;
    font-weight: 500!important;
    font-size: 20px;
    line-height: 160%;
}

@media (max-width: 1439px) {
    .modal-window {
        display: none;
    }
}

.header {
    position: fixed;
    width: 100%;
    max-width: 1920px;
